import { Host, h } from "@stencil/core";
export class RaulListItem {
    render() {
        return (h(Host, null));
    }
    static get is() { return "raul-handle"; }
    static get originalStyleUrls() { return {
        "$": ["raul-handle.scss"]
    }; }
    static get styleUrls() { return {
        "$": ["raul-handle.css"]
    }; }
}
